<template>
    <div>
        <ul>
            <h4>Latest Tv Shows</h4>
            <li v-for="tv in tvs">
            {{tv.name}}
             <LatestTvShows :tv="tv"/>
             </li>
        </ul>
    </div>
</template>
<script>
import LatestTvShows from './LatestTvShows.vue';
export default {
  name: 'LatestTvShowList',  
  data() {
  return {
    tvs:[]
  };
},
created(){
  this.fetchdata();
},
methods: {
  fetchdata: async function(){
    try{
      const res = await fetch(
            'https://api.themoviedb.org/3/tv/top_rated?api_key=a2b41326882d1ffe881d3a949ca7ccc5&language=en-US'
            );
            const tvs = await res.json();
            this.tvs = tvs.results;
         } catch(e){
             console.log(e);
    }
  }
},
components: {
   LatestTvShows,
  }
};
</script>
<style scoped>
ul {
  display: grid;
  list-style: none;
  padding: 1rem;
  margin: 0;
  grid-row-gap: 1rem;
  grid-template-columns: repeat(6, 1fr);
}
</style>





