<template>
     <div class="container">
        <div class="card mb-3"> 
            <img class="card-img-top" src="posterImage" :alt="tv.title"> 
            <div class="card-body">
              <h5 class="card-title">{{tv.title}}</h5>  
          <p class="card-text"><small class="text-muted">>Country Of Origin: {{tv.origin_country}}
               Rating: {{tv.vote_average}} Release Date: {{tv.first_air_date}}</small></p>
          <p class="card-text">{{tv.overview}}</p>
       </div>
      </div>
     </div>
</template>
<script>
 const BACKDROP_PATH = 'http://image.tmdb.org/t/p/w1280';
export default{
    name: 'LatestTvShowDetails',    
 data(){
    return {
    tv:{}
   };
 },
 created(){
   this.fetchData();
 },
 computed:{
 posterImage: function() {
			return `${BACKDROP_PATH}/${this.tv.poster_path}`;
 }
 },
 methods: {
     fetchData: async function(){
         try {
        const res = await fetch(
            `https://api.themoviedb.org/3/tv/${
                this.$route.params.id}
                ?api_key=a2b41326882d1ffe881d3a949ca7ccc5`
            );
            const tv = await res.json();
            this.tv = tv;
         } catch(e){
             console.log(e);
         }
     }
 },  
}
</script>
<style scoped>
tv-wrapper{
    position: relative;
    padding-top: 50vh;
    background-size: cover
}
.tv-info{
    background: white;
    color: #222;
    padding: 2rem 10%;
}
</style>




