import Vue from 'vue';
import Router from 'vue-router';
import MovieList from './components/MovieList.vue';
import MovieDetail from '@/components/MovieDetail';
import TvList from './components/TvList.vue';
import TvDetail from '@/components/TvDetail';
import LatestTvShowList from  './components/LatestTvShowList'
import LatestTvShowDetails from '@/components/LatestTvShowDetails';
Vue.use(Router);

export default new Router({
 mode: 'history',
 routes: [
     {
         path: '/',
         name: 'Movie List',
         component: MovieList
     },{
        path: '/movie/:id',
        name: 'Movie Detail',
        component: MovieDetail
    },
    {
        path: '/tv',
        name: 'Tv List',
        component: TvList
    },{
        path: '/tv/:id',
        name: 'Tv Detail',
        component: TvDetail
    },{
        path: '/latesttv',
        name: 'Latest Tv show List',
        component: LatestTvShowList
    },{
        path: '/latesttv/:id',
        name: 'Latest Tv Show Detail',
        component: LatestTvShowDetails
    }

 ]

});


