<template>
  <div>
    <ul>
    <h4>Popular TV Shows</h4>
    <li v-for="tv in tvs">
      {{tv.name}}
      <Tv :tv="tv"/>
      </li>
      </ul>
  </div>   
</template>

<script>
import Tv from './Tv.vue';
export default {
name: 'TvList',
data(){
  return{
    tvs:[]
  };
},
created(){
  this.fetchdata();
},
methods: {
  fetchdata: async function(){
    try{
      const res = await fetch(
            'https://api.themoviedb.org/3/discover/tv?sort_by=popularity.desc&api_key=a2b41326882d1ffe881d3a949ca7ccc5'
            );
            const tvs = await res.json();
            this.tvs = tvs.results;
         } catch(e){
             console.log(e);
    }
  }
},
components: {
   Tv,
  }
};
</script>

<style>
ul {
  display: grid;
  list-style: none;
  padding: 1rem;
  margin: 0;
  grid-row-gap: 1rem;
  grid-template-columns: repeat(6, 1fr);
}
</style>




