<template>
  <div class="container">
        <div class="col-lg-3 col-md-4 col-xs-6">
    <router-link :to="tvPath">
    <img :src="posterImage" :alt="tv.title"/>
    </router-link>
    </div>
      </div>
</template>
<script>
const POSTER_PATH = 'http://image.tmdb.org/t/p/w400';
export default {
  name: 'Tv',
  props: ['tv'],
  computed: {
		posterImage: function() {
			return `${POSTER_PATH}/${this.tv.poster_path}`;
    },
    tvPath: function() {
      return `/tv/${this.tv.id}`;
    }
	}
};
</script>
<style scoped>
img {
	box-shadow: 0 0 35px black;
	width: 154px;
}

body {
  padding-top: 54px;
}

@media (min-width: 992px) {
  body {
    padding-top: 56px;
  }
}
</style>


