<template>
    <div>
      <div class="tv-wrapper" :style="styles">  
    <div class="tv-info"> 
    <h3>{{tv.title}}</h3>
    <h4>Country Of Origin: {{tv.origin_country}}</h4>
    <h4>Rating: {{tv.vote_average}}</h4>
    <h4>Release Date: {{tv.first_air_date}}</h4>
    <p>{{tv.overview}}</p>
    </div>
</div>
</div>
</template>
<script>
const BACKDROP_PATH = 'http://image.tmdb.org/t/p/w1280';
export default{
    name: 'TvDetail',    
 data(){
    return {
    tv:{}
   };
 },
 created(){
   this.fetchData();
 },
 computed:{
 styles(){
     return{
         background: `url(${BACKDROP_PATH}/${this.tv.backdrop_path}) no-repeat`
     }
 }
 },
 methods: {
     fetchData: async function(){
         try {
        const res = await fetch(
            `https://api.themoviedb.org/3/tv/${
                this.$route.params.id}
                ?api_key=a2b41326882d1ffe881d3a949ca7ccc5`
            );
            const tv = await res.json();
            this.tv = tv;
         } catch(e){
             console.log(e);
         }
     }
 },
}
</script>
<style scoped>
tv-wrapper{
    position: relative;
    padding-top: 50vh;
    background-size: cover
}
.tv-info{
    background: white;
    color: #222;
    padding: 2rem 10%;
}
</style>


